import React from 'react'
import styled from 'styled-components'
import FlexLayout from 'components/layout/Flex'
import { Heading, ButtonMenu, ButtonMenuItem } from 'uikit'
import { PegType } from 'config/constants/types'
import Page from 'components/layout/Page'
import { Link, Route, useRouteMatch } from 'react-router-dom'
import BoardroomToken from './BoardroomToken'

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 32px;
`

export const BoardTabButtons = () => {
  const { url, isExact } = useRouteMatch()

  return (
    <Wrapper>
      <ButtonMenu activeIndex={isExact ? 0 : 1} size="sm" variant="subtle">
        <ButtonMenuItem as={Link} to={`${url}`}>
          SDO
        </ButtonMenuItem>
        <ButtonMenuItem as={Link} to={`${url}/sbnb`}>
          SBNB
        </ButtonMenuItem>
      </ButtonMenu>
    </Wrapper>
  )
}

const Boardroom: React.FC = () => {
  const { path } = useRouteMatch()

  return (
    <Page>
      <FlexLayout>
        <Heading as="h1" size="lg" mb="20px" style={{ textAlign: 'center' }}>
          ðŸ’± Boardroom
        </Heading>
        <BoardTabButtons />
      </FlexLayout>
      <Route exact path={`${path}`}>
        <BoardroomToken type={PegType.SDO} />
      </Route>
      <Route exact path={`${path}/sbnb`}>
        <BoardroomToken type={PegType.SBNB} />
      </Route>
    </Page>
  )
}

export default Boardroom
